import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
} from 'react-router-dom';
import SignIn from '../module/sign-in/components/sign-in.component';
import {
    RegisterData,
    SignUp,
} from '../module/sign-up/components/sign-up.component';
import { Home } from '../module/home/components/home.component';

import css from './app.module.css';

export class App extends React.PureComponent {
    handleSignUp = async (data: RegisterData) => {
        console.log('handleSignUp', data, JSON.stringify(data));
        try {
            const res = await fetch('/users', {
                method: 'POST',
                mode: 'no-cors',
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                },
                body: JSON.stringify(data),
            });
            console.log('res', res);
        } catch (e) {
            console.error(e);
        }
    };

    render() {
        return (
            <div className={css.container}>
                <Router>
                    <Switch>
                        <Route path="/sign-up" exact={true}>
                            <SignUp onSubmit={this.handleSignUp} />
                        </Route>
                        <Route path="/sign-in" exact={true}>
                            <SignIn />
                        </Route>
                        <Route path="/" exact={true}>
                            <Home />
                        </Route>
                        <Redirect to={'/'} />
                    </Switch>
                </Router>
            </div>
        );
    }
}
